.logo-wrap {
  display: flex;
  align-items: center;
  justify-self: center;
  position: relative;
  margin: 4px 10px 4px 30px;
  
  .logo-svg {
    position: relative;
    width: 30px;
    height: 30px;
    z-index: 3;
  }

  &:hover {
    &:before {
      filter: brightness(1.2);
    }
  }

  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 50%;
    box-shadow: 
      0px 0px 80px 5px rgb(255, 1, 255),
      60px -40px 70px 5px rgb(0, 162, 255),
      -60px -40px 70px 5px rgb(14, 166, 255),
      0px -40px 60px 5px rgb(87, 193, 255);
    pointer-events: none;
    animation: time 2s 1.4s forwards;
    opacity: 0;
    filter: brightness(1);
    transition: .5s;
  }

  
  .logo-circle-3 {
    fill: rgb(250, 250, 250);
  } 
  .logo-circle-2 {
    fill: #444444;
  } 
  .logo-circle-1 {
    fill: #4d4d4d;
  }
}

@keyframes rotateShadow {
  0% {transform: rotate(0);}
  100% {transform: rotate(360deg);}
}

@keyframes time {
  0% {opacity: 0;}
  100% {opacity: 1;}
}